@mixin font($size, $weight) {
	font-size: $size;
	font-weight: $weight;
}

@mixin absolute($top, $right, $bottom, $left, $width, $height) {
	position: absolute;
	top: $top;
	right: $right;
	bottom: $bottom;
	left: $left;
	width: $width;
	height: $height;
}

@mixin flex($flex-direction, $justify-content, $align-items) {
	display: flex;
	flex-direction: $flex-direction;
	justify-content: $justify-content;
	align-items: $align-items;
}

@mixin icon($wh, $img-name) {
	content: '';
	display: block;
	background: url(../img/icon/#{$img-name}.png) no-repeat center / #{$wh}px #{$wh}px;
	width: $wh + px;
	height: $wh + px;
	box-sizing: border-box;
}

@mixin spliteBtn($wh, $background-positionX, $background-positionY) {
	background: url(../img/splite.png) no-repeat $background-positionX $background-positionY;
	background-size: 500%;
	width: $wh;
	height: $wh;
}

@mixin borderGradient2P($start-color, $start-point, $end-color, $end-point, $px: 2px) {
	border-bottom: $px solid;
	border-image: linear-gradient(to bottom, $start-color $start-point, $end-color $end-point);
	border-image-slice: 1;
}
@mixin borderGradient3P($start-color, $start-point, $mid-color, $mid-point, $end-color, $end-point, $px: 2px) {
	border-bottom: $px solid;
	border-image: linear-gradient(to right, $start-color $start-point, $mid-color $mid-point, $end-color $end-point);
	border-image-slice: 1;
}

@mixin scrollBar() {
	&::-webkit-scrollbar {
		width: 20px;

		&-track {
			background-color: #fbfbf8;
		}
		&-thumb {
			border-radius: 4px;
			border-top: 2px solid #e0e7ef;
			border-bottom: 2px solid #e0e7ef;
			background-image: url(../img/thumb.png);
			background-size: 20px 1px;
		}
		&-button {
			@include spliteBtn(20px, 0, 0);

			&:start:decrement {
				display: block;
				height: 20px;
				background-position: -80px 0;
			}
			&:end:increment {
				display: block;
				height: 20px;
				background-position: -80px -20px;
			}
		}
	}
}
